/* eslint-disable no-eval */
export default {
  data() {
    return {
      _golden_tax_url: '',
      _synthesisUtterance: null,
      window: window,
      语音输入: false,
      语音播报: false,
      // 用户输入的问题
      question: '',
      // 聊天记录
      chat: JSON.parse(localStorage['wenda_chat_history'] || '[]'),
      // 是否开启历史记录
      history_on: false,
      // 历史记录的数量限制
      history_limit: 5,
      // 功能菜单
      func_menu: [],
      // 当前选中的功能
      current_func: '',
      // 知识库检索策略
      zsk_libraryStategy: '',
      // 知识库检索结果数量
      zsk_maxItmes: 5,
      // 知识库总结问题的提示
      zsk_summarize_prompt: '总结以下文段中与问题相关的信息。',
      // 知识库回答问题的提示
      zsk_answer_prompt: '学习以下文段,用中文回答问题。',
      // 载入的auto
      func_mode: {
        name: '',
        description: 'input_question',
        question: ''
      },
      // 插件列表
      plugins: [],
      // 按钮列表
      buttons: [],
      // auto列表
      autos: [],
      // 是否简化历史记录
      simplify_historical_information: false,
      // 是否开启语音合成
      tts_on: false,
      // 是否开启语音识别
      sst_started: false,
      // 测试知识库的提示
      test_zsk_prompt: '',
      // 测试知识库的步骤
      zsk_step: 2,
      // 知识库
      zhishiku: [],
      // 设置
      setting: {},
      // 当前选中的tab
      tab: 0,
      // 生成回答的温度
      temperature: 0.8,
      // 生成回答的最大长度
      max_length: 2048,
      // 生成回答的top_p
      top_p: 0.8,
      // 语言模型类型
      llm_type: '',
      // tab列表
      tabs: ['chat', 'zhishiku', 'setting'],
      // 是否显示snackbar
      snackbar: false,
      // snackbar的文本
      snackbar_text: '',
      // 是否正在加载
      loading: false,
      // 是否显示左侧菜单
      drawer: false,
      // 是否显示顶部菜单栏，逻辑中根据插件的hide_title属性来判断是否需要隐藏
      show_header: true,
      // 主题色
      color: '#0077F1', // 'purple',
      // 管理界面中，搜索auto的关键词
      autos_search: ''
    }
  },
  computed: {
    filteredFuncMenu() {
      return this.func_menu.filter(c => c.name && !window._hidden_func_in_menus.includes(c.name))
    }
  },
  created() {
    // 组件实例暴露到windows
    window.app = this
  },
  beforeDestroy() {
    window.app = null
    if (speechSynthesis) {
      // console.log('speechSynthesis', speechSynthesis)
      // console.log('speechSynthesis voices', speechSynthesis.getVoices())
      // // speechSynthesis.stop()
    }
  },
  mounted() {
    const { _golden_tax_url, _autos_js_loaded } = this.$util.getAppSettings()
    this._golden_tax_url = _golden_tax_url
    this.dynamicLoadScript('/api/llm').then(() => {
      this.loadAutoJSFuctions(_autos_js_loaded).then(async() => {
        this.func_menu = window.func
        // this.load_plugins()
      })
    })
  },
  watch: {
    current_func(current_func) {
      this.func_mode = this.func_menu.find((i) => i.name === current_func)
    },
    tab(val) {
      if (val > 2) {
        if (this.plugins[val - 3].hide_title === true) {
          this.show_header = false
          return
        }
      }
      this.show_header = true
    }
  },
  methods: {
    getMenuIcon(func) {
      if (func.icon) {
        return `mdi-${func.icon}`
      }
      const icon = window._menu_icon_func[func.name]
      if (icon) {
        return `mdi-${icon}`
      }
      return 'mdi-clock'
    },
    // 加载所有插件并将其添加到 app.autos 数组中
    async load_plugins() {
      window.disabled_auto = JSON.parse(localStorage['wenda_disabled_auto'] || '{}')
      let server_auto = await fetch('/api/plugins')
      server_auto = await server_auto.json()
      server_auto.forEach((plugin) => {
        const name = window.get_auto_name(plugin)
        const auto = {
          name: name,
          description: window.get_auto_description(plugin),
          content: plugin.content,
          disabled: window.get_auto_disabled(plugin, name)
        }
        this.autos.push(auto)
        setTimeout(() => {
          if (!auto.disabled) eval(plugin.content)
        }, 0)
      })

      window.saved_auto = JSON.parse(localStorage['wenda_saved_auto'] || '[]')
      window.saved_auto.forEach((auto) => {
        this.autos.push(auto)
        setTimeout(() => {
          if (!auto.disabled) eval(auto.content)
        }, 0)
      })
    },
    saveSettingAutos() {
      const disabled_auto = {}
      this.autos.forEach(auto => { disabled_auto[auto.name] = auto.disabled })
      window.localStorage['wenda_disabled_auto'] = JSON.stringify(disabled_auto)
    },
    clear_history() {
      this.chat = []
      this.save_history()
      this.cancel_speaking()
    },
    cancel_speaking() {
      if (speechSynthesis.speaking) {
        if (typeof speechSynthesis.cancel === 'function') {
          speechSynthesis.cancel()
        } else {
          speechSynthesis.stop()
        }
      }
    },
    /**
     *  播放合成语音 SynthesisUtterance
     *
     * @param {*} s
     * @param {*} config pitch[0-2] volume[0-1]
     */
    speak(s, config) {
      // this.cancel_speaking()
      if (!this._synthesisUtterance) {
        this._synthesisUtterance = new SpeechSynthesisUtterance()
      }
      this._synthesisUtterance.rate = 1
      this._synthesisUtterance.pitch = 10
      this._synthesisUtterance.text = s
      this._synthesisUtterance.volume = 1
      if (config) {
        Object.assign(this._synthesisUtterance, config)
      }
      speechSynthesis.speak(this._synthesisUtterance)
    },
    onQuestionChanged(question) {
      this.question = question
      window.submit()
    },
    save_history() {
      localStorage['wenda_chat_history'] = JSON.stringify(this.chat)
    },
    resetConversitionMode(func) {
      this.current_func = func || ''
    },
    alert(text) {
      this.snackbar_text = text// .replace(/\n/g,"<br>")
      this.snackbar = true
    },
    dynamicLoadScript(src) {
      return new Promise((resolve, reject) => {
        const autojs = document.createElement('script')
        autojs.src = src
        autojs.type = 'text/javascript'
        autojs.onload = function() {
          resolve()
        }
        autojs.onerror = function(err) {
          console.error(err)
          reject(new Error('Failed to load ' + src))
        }
        document.body.appendChild(autojs)
      })
    },
    loadAutoJSFuctions(auto_menus) {
      return Promise.all(auto_menus.map(src => {
        return this.dynamicLoadScript(`/autos/${src}.js`)
      }))
    },
    scrollToBottom() {
      this.$nextTick(() => {
        window.scrollTo(0, document.body.scrollHeight)
      })
    },
    /**
     * 打字机效果
     *
     * @param {*} talk 对话对象 格式： { role: "AI", content: '……' }
     * @param {*} prop 属性标题
     * @param {*} resData 返回数据
     * @param {*} speak 语音播报
     */
    loopPrintWords(talk, prop, resData, speak) {
      talk.content = `#### ${prop}: \n`
      this.chat.push(talk)
      const contentWords = resData
      if (speak) {
        const trimedContent = (talk.content + contentWords).replace(/[#\s]*/, '')
        this.cancel_speaking()
        this.speak(trimedContent, { rate: 1, pitch: 1 })
      }

      let i = 0
      const timer = setInterval(() => {
        if (i < contentWords.length) {
          talk.content += contentWords[i]
          if (contentWords[i] === '\n') {
            this.scrollToBottom()
          }
          i++
        } else {
          clearInterval(timer)
        }
      }, 100)
    }
  }
}
