    <v-card elevation="2">
      <v-card-title>模型运行参数</v-card-title>
      <v-divider />
      <v-card-text>
        <br>
        <v-form>
          <v-slider
            v-model="max_length"
            :min="1000"
            :max="4000"
            :step="10"
            :thumb-color="color"
            label="max_length"
            thumb-label
            @change="(v)=>$emit('max_length-changed',v)"
          >
            <template v-slot:append>
              <v-text-field
                v-model="max_length"
                class="mt-0 pt-0"
                type="number"
                style="width: 60px"
                @change="(v)=>$emit('max_length-changed',v)"
              />
            </template>
          </v-slider>
          <v-slider
            v-model="temperature"
            :min="0"
            :max="2"
            :step="0.1"
            :thumb-color="color"
            label="temperature"
            thumb-label
            @change="(v)=>$emit('temperature-changed',v)"
          >
            <template v-slot:append>
              <v-text-field
                v-model="temperature"
                class="mt-0 pt-0"
                type="number"
                style="width: 60px"
                @change="(v)=>$emit('temperature-changed',v)"
              />
            </template>
          </v-slider>
          <v-slider
            v-model="top_p"
            :min="0"
            :max="1"
            :step="0.1"
            :thumb-color="color"
            label="top_p"
            thumb-label
            @change="(v)=>$emit('top_p-changed',v)"
          >
            <template v-slot:append>
              <v-text-field
                v-model="top_p"
                class="mt-0 pt-0"
                type="number"
                style="width: 60px"
                @change="(v)=>$emit('top_p-changed',v)"
              />
            </template>
          </v-slider>
          <v-row>
            <v-col
              cols="12"
              sm="2"
            >
              <v-switch
                v-model="simplify_historical_information"
                hide-details
                :label="$t('simplify_historical_information')"
                :color="color"
                inset
                @change="(v)=>$emit('simplify_historical_information-changed',v)"
              />
            </v-col>
            <v-col
              cols="12"
              sm="10"
            >
              <v-slider
                v-model="history_limit"
                :min="0"
                :max="10"
                :step="1"
                :thumb-color="color"
                label="对话轮数限制"
                thumb-label
                @change="(v)=>$emit('history_limit-changed',v)"
              >
                <template v-slot:append>
                  <v-text-field
                    v-model="history_limit"
                    class="mt-0 pt-0"
                    type="number"
                    style="width: 60px"
                    @change="(v)=>$emit('history_limit-changed',v)"
                  />
                </template>
              </v-slider>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
    </v-card>
  </v-tab-item>
</template>

<script>
export default {
  name: 'TabItemSetting',
  props: {
    maxLength: {
      type: Number,
      default: 2048
    },
    propTemperature: {
      type: Number,
      default: 0.8
    },
    topP: {
      type: Number,
      default: 0.8
    },
    simplifyHistoricalInformation: {
      type: Boolean,
      default: false
    },
    historyLimit: {
      type: Number,
      default: 5
    },
    defaultColor: {
      type: String,
      default: '#0077F1'
    }
  },
  computed: {
    max_length: {
      get() {
        return this.maxLength
      },
      set(v) {
        this.$emit('max_length-changed', v)
      }
    },
    temperature: {
      get() {
        return this.propTemperature
      },
      set(v) {
        this.$emit('temperature-changed', v)
      }
    },
    top_p: {
      get() {
        return this.topP
      },
      set(v) {
        this.$emit('top_p-changed', v)
      }
    },
    simplify_historical_information: {
      get() {
        return this.simplifyHistoricalInformation
      },
      set(v) {
        this.$emit('simplify_historical_information-changed', v)
      }
    },
    history_limit: {
      get() {
        return this.historyLimit
      },
      set(v) {
        this.$emit('history_limit-changed', v)
      }
    },
    color() {
      return this.defaultColor
    }
  },
  methods: {
  }
}
</script>

